.mobile-help{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contenedor-botones_desktop{
    display: none;
}

.contenedor-bordes_detalles{
    display: none;
}
.contenedor-bg{
    background-color: $beishe;
    width: 100%;
    height: 85vh;
    margin-bottom: 3rem;
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    
}

.img-servicios{
    display: none;
}

.hogar-image{
    margin: -1rem 0 0 -1.2rem ;
}

.corporativo-image{
    margin: -1rem 0 0 .5rem;
}

.seccion-corporativo, .seccion-hogar{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    .helper{
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-around;
        align-items: center;
    }
    .btn-simple{
        width: 90%;
        p{
            margin: 0;
            line-height: 1;
            font-size: 2rem;
        }

        @include telefonoPequeno {
            width: 70%;
            padding: 2rem 0;
            margin: 0 -3rem;
            P{
                font-size: 2rem;
            }
        }
    }
}

.seccion-hogar{
    flex-direction: row-reverse;
}



// Fondo de las imagenes con bordes negros

.contenedor-img{
    @include desktop{
        border: solid black .3rem;
    }
 
    width: 20rem;
    height: 100%;
}

//Carrusel 

.contenedor-carrusel{
    display: flex;
    flex-direction: column;
    margin:0;
    align-items: center;
    height: auto;
    .titulo-producto{
        font-weight: $bold;
        font-size: 2.5rem;
        margin: 1.5rem;
    }
    .contenedor-imagen{
        width: 90%;
    }
    .contenedor-bordes, .contenedor-bordes_detalles {
        margin: 3rem 0;
        border: solid $black .5rem;
        height: 20rem;
        width: 30rem;
        .contenedor-descripcion{
            width: 100%;
            height: 100%;
            background-color: $beishe;
            padding: 2rem;
            margin: -1rem;
            

            .producto-titulo{
                font-weight: $regular;
                font-size: 2rem;
                margin: 0;
                text-align: center;
            }
            .producto-descripcion{
                font-size: 1.5rem;
                margin: 0;
                text-align: justify;
            }
        }
    }

    .contenedor-carrusel_btn{
        display: flex;
        justify-content: space-between;
        margin: 1rem 0;
    }

    
}

//desktop

@include desktop{
    .contenedor-bordes_detalles{
        display: flex;
    }
    .img-servicios{
        display: flex;
    }

    .bg-nosotros_principal{
        z-index:-1;
    }
    .contenedor-bg
    {
        background: white;
        flex-direction: row;
        height: 80vh;
        align-items: center;
    }

    .seccion-corporativo, .seccion-hogar{
        flex-direction: column-reverse;
        width: 30%;
        height: 85%;
        
        .helper{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            position: absolute;
            height: 50%;
        }

        .btn-simple{
            position: relative;
            width: 20rem;
            p{
                font-size: 2rem;
            }
            &:hover{
                background-color: white;
                color: black;
                p{
                font-size: 2.2rem;
                font-weight: $bold;
                }
            }
        }
        .contenedor-img{
            width: 100%;
            height: 100%;
            border: 1rem solid #000;
            display: flex;
            align-items: center;
            justify-content: center;

            .img-servicios{
                width: 100%;
                height: 100%;
            }

            .hogar-image{
             margin: -1.5rem 0 0 -1.2rem;
            }

            .corporativo-image{
                margin: -1.5rem 0 0 -1.2rem;
            }
        }
    }

    .contenedor-carrusel{
        .display{
            display: none;
        }

        .titulo-producto{
            font-size: 3rem;
            margin: 0;
        }

        .contenedor-imagen{
            width: 32%;
            margin-top: -18rem;
            margin-right: -5rem;
        }

        .mobile-help{
        display: flex;
        flex-direction: row;
        height: 75vh;

        }

    

        .contenedor-bordes, .contenedor-bordes_detalles{
            width: 30rem;
            height: 20rem;
            border: solid $black 1rem;
            margin-bottom: -20rem;
            margin-left: 6rem;
           
            .contenedor-descripcion{
                width: 95%;
                height: 95%;
                margin: 1rem auto;
                padding: 1rem;
                .producto-titulo, .producto-descripcion{
                    font-size: 1.5rem;
                    line-height: 2rem;
                    margin: .5rem auto;
                }
            }
        }

        .contenedor-bordes_detalles{
            margin-top: -38rem;
            margin-left: -2rem;
            z-index: -1;
        }

        .btn-simple{
            margin: 1rem 1rem;
            padding: .5rem;
            p{
                font-size: 2rem;
            }
        }
        
    }
}